@font-face {
  font-family: "Nexa Light";
  src: url("../assets/fonts/nexa/Nexa Light.otf"); }

@font-face {
  font-family: "Nexa Bold";
  src: url("../assets/fonts/nexa//Nexa Bold.otf"); }

@font-face {
  font-family: "Nunito Sans Regular";
  src: url("../assets/fonts/nunito/NunitoSans-Regular.ttf"); }

@font-face {
  font-family: "Nunito Sans ExtraBold";
  src: url("../assets/fonts/nunito/NunitoSans-ExtraBold.ttf"); }

@font-face {
  font-family: "Nunito Sans SemiBold";
  src: url("../assets/fonts/nunito/NunitoSans-SemiBold.ttf"); }

@font-face {
  font-family: "Nunito Sans Bold";
  src: url("./../assets/fonts/nunito/NunitoSans-Bold.ttf"); }

@font-face {
  font-family: "Nunito Sans Black";
  src: url("../assets/fonts/nunito/NunitoSans-Black.ttf"); }

.heading-font {
  font-family: Nexa Bold; }

.largetext-font {
  font-family: Nexa Light; }

.text-font-regular {
  font-family: 'Nunito Sans Regular'; }

.text-font-semibold {
  font-family: 'Nunito Sans SemiBold'; }

.text-font-extrabold {
  font-family: 'Nunito Sans ExtraBold'; }

.text-font-bold {
  font-family: 'Nunito Sans Bold'; }

.text-font-black {
  font-family: 'Nunito Sans Black'; }

body {
  background: #2e2c2c;
  overflow-x: hidden; }

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0; }

input {
  outline: none; }

::-webkit-scrollbar {
  width: 10px; }

::-webkit-scrollbar-track {
  background: #413f3f;
  border-radius: 5px; }

::-webkit-scrollbar-thumb {
  background: #2e2c2c;
  border-radius: 1px;
  box-shadow: inset 0 0 #1f1e1e;
  border: 1px solid #413f3f; }

::-webkit-scrollbar-thumb:hover {
  background: #1a1919; }

::-webkit-scrollbar-button {
  display: block;
  height: 2px;
  background-color: #2e2c2c; }

.row {
  margin: 0;
  padding: 0; }

.text-color-light {
  color: #f7eeee; }

.content-color {
  color: #dddcdc; }

.linkstyle {
  font-family: 'Indie Flower', cursive;
  color: #bcbcbd;
  text-decoration: none;
  padding: 2%; }
  .linkstyle:hover {
    color: #f7eeee;
    align-items: center;
    box-shadow: 2px 2px 5px 0 rgba(29, 27, 27, 0.89), -3px -3px 2px 0 rgba(94, 88, 88, 0.658);
    border-radius: 10%; }

.header .user-header {
  min-height: 4vh;
  background-color: #413f3f; }
  .header .user-header .user-pic {
    background-color: white;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    margin-right: 0.5rem;
    padding: 0.5rem; }

.header .navigation {
  background-color: #bb1280;
  min-height: 6vh; }
  .header .navigation .search_bar {
    color: white; }
    .header .navigation .search_bar input {
      background-color: #bb1280;
      border: none;
      appearance: none;
      border: inset 2px #f7eeee; }
      .header .navigation .search_bar input:focus {
        color: #f7eeee; }
  .header .navigation .menu {
    min-width: 16vw; }

.sidenav {
  height: 100vh;
  position: fixed;
  z-index: 2002;
  top: 0;
  left: 0;
  background-color: #111;
  transition: 0.5s; }
  .sidenav a,
  .sidenav h2 {
    padding: 9%;
    text-decoration: none;
    color: #818181;
    display: block;
    transition: 0.3s; }
    .sidenav a:hover,
    .sidenav h2:hover {
      color: #f1f1f1;
      cursor: pointer; }
  .sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 20px; }

.overlay {
  min-height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2001;
  background-color: rgba(250, 250, 250, 0.171); }

.floating-menu {
  position: absolute;
  z-index: 2000;
  top: 11vh;
  right: 2vh;
  cursor: pointer;
  color: #f1f1f1;
  border-radius: 12%; }

@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 15px; }
    .sidenav a {
      font-size: 18px; } }

.recents-container {
  background-color: #b13385;
  direction: rtl;
  position: fixed;
  margin-top: 5%;
  margin-bottom: 0;
  box-shadow: -7px -7px 13px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  height: 85%; }
  .recents-container .recents-heading h2 {
    padding-top: 10px;
    color: #fff;
    text-align: center; }
  .recents-container .scrollbar {
    background-color: #413f3f;
    border-left: 0.8px solid #3f3f3f;
    height: inherit;
    overflow-x: hidden;
    overflow-y: scroll; }
  .recents-container .news-section {
    direction: ltr; }
    .recents-container .news-section .col-md-4 {
      padding: 3px; }

.interests-section {
  margin: 3%; }
  .interests-section .interestseccard {
    box-shadow: -7px -7px 13px rgba(126, 124, 124, 0.2), 10px 10px 13px rgba(0, 0, 0, 0.959);
    border-radius: 3px; }
    .interests-section .interestseccard .card-header {
      margin: 2%;
      border-bottom-color: #6c757d;
      color: #fff; }

.card {
  background-color: #2e2c2c !important; }

.container-fluid {
  padding: 0; }
  .container-fluid .row .col-8 {
    margin: 0;
    padding: 0; }
  .container-fluid .row .col {
    margin: 0;
    padding: 0; }

.horizontalcards {
  margin: 5%;
  box-shadow: -7px -7px 13px rgba(126, 124, 124, 0.2), 10px 10px 13px rgba(0, 0, 0, 0.959);
  background-color: #2e2c2c !important;
  max-height: 160px; }
  .horizontalcards .card-body {
    padding: 10px 5px 0 5px; }
  .horizontalcards .col-md-4 {
    max-height: 158px; }
  .horizontalcards .card-body .card-title {
    color: #f7eeee;
    max-height: 36px;
    font-size: 16px;
    overflow: hidden;
    margin-bottom: 1rem; }
  .horizontalcards .card-body .card-text {
    font-family: 'Roboto', sans-serif;
    overflow: hidden;
    line-height: 1.2;
    font-size: 14px;
    max-height: 52px;
    word-spacing: normal; }
  .horizontalcards .card-body .article-link {
    text-decoration: none; }

.page {
  margin: 2%;
  margin-top: 0; }
  .page .col .business {
    margin-top: 80px; }
    .page .col .business .sqrcards {
      margin-right: 10%;
      background-color: #2e2c2c !important;
      max-height: 380px;
      box-shadow: -7px -7px 13px rgba(126, 124, 124, 0.2), 10px 10px 13px rgba(0, 0, 0, 0.959);
      border-radius: 3px; }
      .page .col .business .sqrcards .cardtext {
        font-family: 'Roboto', sans-serif;
        overflow: hidden;
        line-height: 1.2;
        font-size: 14px;
        max-height: 35px;
        word-spacing: normal; }
      .page .col .business .sqrcards .card-header {
        margin: 2%;
        border-bottom-color: #6c757d;
        color: #fff; }
        .page .col .business .sqrcards .card-header .card-title {
          color: #f7eeee;
          max-height: 20px;
          font-size: 18px;
          overflow: hidden;
          margin-bottom: 1rem; }
